.header {
    grid-area: h;
    background-color: #7b7b7b;
    display: flex;
    font-family: 'Euphoria Script', cursive;
    align-items: center;
    justify-content: space-around;
    border-bottom: 1px solid black;
    border-radius: 10px;
    
}

.header img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.header h2 {
    font-size: 40px;
}