

.user {
    display: grid;
    grid-template-columns: 2fr 10fr;
    padding: 20px;
    margin-bottom: 10px;
    background-color: #bca9e2;
    opacity: 0.7;
    border-radius: 50px;
}

.userPhoto {
    width: 80px;
    height: 70px;
    border-radius: 50%;
}
.user_info{
    display: flex;
    justify-content: space-between;
    padding: 15px;
}
.img_block{
    align-items: center;
}
.follow_btn{
    display: block;
}
.selectedPage{
    font-weight: bold;
    color: red;
}