.nav {
    grid-area: n;
    background-color: #7b7b7b;
    border-right: 1px solid black;
    margin-top: 10px;
    border-radius: 10px;
    padding-top: 15px;
    padding-left: 15px;
    height: 500px;
}

.nav_item {
    cursor: pointer;
    text-decoration: none;
    color: black;
}

.nav_item.active {
    color: red;
}

.nav_link {
    max-width: 50px;
    margin-bottom: 7px;
}

.nav_item:hover {
    background-color: rgb(168, 39, 168);
}

.friends {
    box-shadow: 0 0 5px 2px black;
    width: 155px;
    padding: 10px;
    margin-top: 50px;
}

.friends h2 {
    margin: 0 0 10px;
    text-align: center;
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: red;
}

.friends_items {
    display: inline-block;
    margin-right: 10px;
}