
.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    background-color: #7b7b7b;
    border-radius: 20px;
    max-height: 100%;
}

.dialogs_item {
    padding: 10px
}

.item {
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 10px;
    color: black;
    text-decoration: none;
}

.messages {
    padding: 10px;
}

.message {
    padding: 10px
}

.dialogs_item.active {
    color: red;
}

.item_avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: bisque;
    margin-right: 10px;
}
.message_item{
    display: flex;
    justify-content:left ;
    align-items: center;
}